

* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to top, rgba(14, 0, 216, 0.459) 50%, rgba(208, 255, 0, 0.582) 50%);
  color: #212121;
  line-height: 1.5;
}

ul{
  list-style: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border: solid 2px #0502af;
}
#search-box:hover,
#search-box:focus {
  outline: none;
  background-color:rgb(239, 255, 12);
}
.bigflag{
  width: 200px;
  height: auto;
  display: block;
}

.country-info{
  width: 500px;
}

.country-info__name{
  margin: 0;
}

.country{
  margin: 0;
  padding-left: 0;
}
.country-list{
  padding-left: 0px;
}

.country-list__item {
  display: flex;
  align-items: center;
}

.flag {
  width: 30px;
}
.country-list__name{
  margin-left: 10px;
  font-size: 16px;
}
